* {
  padding: 0;
  border: none;
  outline: none;
}

body {
  background: url(../src/accueil.webp);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}
@media screen and (max-width: 540px) {
  body {
    background-position: right;
  }
}

.contact-form {
  margin: 3rem auto 0;
  max-width: 50%;
  max-width: 450px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: 1px solid #333;
  padding: 0 30px 10px;
  box-shadow: 0px 4px 10px rgba(51, 51, 51, 0.637);
  border-radius: 5px;
}
@media screen and (max-width: 620px) {
  .contact-form {
    max-width: 350px;
  }
}

@media screen and (max-width: 520px) {
  .contact-form {
    margin: 12rem auto 0;
    background: rgb(218 213 213 / 95%);
  }
}
@media screen and (max-width: 420px) {
  .contact-form {
    max-width: 320px;
  }
  h2 {
    font-size: 18px;
  }
  span {
    text-align: center;
    line-height: 1.5;
    font-size: 14px;
  }
}
.logo {
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
h2 {
  margin-top: 15px;
  margin-bottom: 10px;
  text-align: center;
}
span {
  display: flex;
  justify-content: center;
  font-weight: 500;
}

.form-content {
  display: grid;
  grid-template-rows: repeat(4, 1fr) 105px;
}
input,
textarea {
  background: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.1rem;
  border-bottom: 1px solid#333;
  color: #000;
}
input::placeholder,
textarea::placeholder {
  color: rgb(39, 39, 39);
}
input,
.email-content,
textarea {
  width: 90%;
  margin: 0 auto;
}
input {
  padding: 20px 0 12px;
}
.email-content {
  position: relative;
}
.email-content input {
  width: 100%;
}
#not-mail {
  display: none;
  position: absolute;
  top: 4px;
  color: #c04141;
  right: 0;
  transform-origin: 50% 50%;
}
@keyframes dongle {
  0% {
    transform: translate(0px, 0px);
  }
  10% {
    transform: translate(-10px, 0px);
  }
  20% {
    transform: translate(10px, 0px);
  }
  30% {
    transform: translate(-10px, 0px);
  }
  40% {
    transform: translate(10px, 0px);
  }
  50% {
    transform: translate(-10px, 0px);
  }
  60% {
    transform: translate(10px, 0px);
  }
  70% {
    transform: translate(-10px, 0px);
  }
  80% {
    transform: translate(10px, 0px);
  }
  90% {
    transform: translate(-10px, 0px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
textarea {
  resize: none;
  padding: 20px 0;
}
.button {
  font-size: 1.3rem;
  margin-top: 15px;
  color: #333;
  border: none;
  cursor: pointer;
  transition: 0.2s;
  width: 100%;
}
.button:hover {
  letter-spacing: 2px;
}
.error::placeholder {
  color: #c04141;
}
.form-message {
  margin-top: 10px;
  padding: 12px;
  opacity: 0;
  transition: 0.2s ease;
  color: black;
  border-radius: 4px;
  box-shadow: 0 0 2px rgba(51, 51, 51, 0.3);
}
